.app-container {
  display: flex;
  flex-direction: column;
  height: 100vh;
  width: 100vw;
  background-color: #f0f0f0;
}

.chat-header {
  background-color: #eee;
  padding: 10px;
  text-align: center;
  flex-shrink: 0; /* Evita que el header se encoja */
}

.chat-body {
  flex-grow: 1;
  overflow-y: auto;
  padding: 10px;
}

.chat-footer {
  display: flex;
  justify-content: center; /* Centra los elementos horizontalmente */
  align-items: center;
  padding: 20px;
  background-color: #fff;
  flex-shrink: 0; /* Evita que el footer se encoja */
}

.message {
  margin-bottom: 10px;
}

.user {
  text-align: right;
}

.user p {
  background-color: #DCF8C6;
  padding: 10px;
  border-radius: 10px;
  display: inline-block;
}

.bot {
  text-align: left;
}

.bot p {
  background-color: #ADD8E6;
  padding: 10px;
  border-radius: 10px;
  display: inline-block;
}

button {
  background-color: #4CAF50; /* Green */
  border: none;
  color: white;
  padding: 15px 32px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  border-radius: 5px;
  cursor: pointer;
}

.loading-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5); /* Difuminado */
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 10; /* Asegúrate de que la capa de carga esté por encima del resto del contenido */
}

.loading-icon {
  width: 64px;
  height: 64px;
}